import { Toast } from "@ionic-native/toast";
import { HttpClient } from "@angular/common/http";
import { Component } from "@angular/core";
import { Storage } from "@ionic/storage";
import { NavController, Events, AlertController } from "ionic-angular";
import { Mixpanel } from "@ionic-native/mixpanel";


@Component({
  selector: "page-new-note",
  templateUrl: "new-note.html"
})
export class NewNotePage {
  user: any;
  today: Date;
  title: String = "";
  preacher: String;
  content: String;
  canLeave: boolean = false;
  modules = {
    toolbar: [
      [
        "bold",
        "italic",
        "blockquote",
        { list: "ordered" },
        { list: "bullet" },
        { size: ["small", false, "large", "huge"] },
        "link",
        "image",
        "video",
        [{ font: ["Brandon Grotesque Light"] }]
      ]
    ]
  };

  constructor(
    public navCtrl: NavController,
    public storage: Storage,
    public http: HttpClient,
    public toast: Toast,
    public events: Events,
    public alertCtrl: AlertController,
    public mixpanel: Mixpanel
  ) {
    this.storage.get("user").then(user => {
      this.user = user;
    });
    this.today = new Date();
    this.mixpanel.track("New Note");
  }

  // ionViewWillLeave() {
  //   if (this.title || this.content.value) {
  //     let alert = this.alertCtrl.create({
  //       title: "Confirm purchase",
  //       message: "Do you want to buy this book?",
  //       buttons: [
  //         {
  //           text: "Cancel",
  //           role: "cancel",
  //           handler: () => {
  //             this.canLeave = false;
  //           }
  //         },
  //         {
  //           text: "Buy",
  //           handler: () => {
  //             this.canLeave = true;
  //           }
  //         }
  //       ]
  //     });
  //     alert.present();
  // }

  // ionViewCanLeave(): boolean {
  //   return true;
  // return this.title === "" || this.content.value === "" ? true : false;
  // if (this.canLeave) {
  //   return true;
  // } else {
  //   return false;
  // }
  // }

  saveNote() {
    this.uploadNote();
    let note = {
      title: this.title,
      content: this.content,
      created_at: this.today
    };
    this.events.publish("note:created", note);
    this.navCtrl.pop();
  }

  uploadNote() {
    this.http
      .post("https://realm-glory.herokuapp.com/api/notes", {
        title: this.title,
        preacher: this.preacher,
        content: this.content,
        user: this.user._id
      })
      .subscribe(status => {
        this.storage.set("notes", status["data"]);
      });
  }
}



